import {LanguageSupport} from "@codemirror/language"
import {completeFromList} from "@codemirror/autocomplete"
import {LRLanguage} from "@codemirror/language"
import {foldNodeProp, foldInside, indentNodeProp} from "@codemirror/language"
import {styleTags, tags as t} from "@lezer/highlight"
import {buildParser} from "@lezer/generator"

import grammar from './metamath.grammar' 

export function MMLanguage(){

    console.log('MMLanguage start')

    let parser = buildParser(grammar)

    console.log('parser')

    let parserWithMetadata = parser.configure({
        props: [
          styleTags({
            AxiomStart: t.string,
            ConstantStart: t.string,
            VariableStart: t.string,
            DeclarationStart: t.string,
            ProofStart: t.string,
            ProofStepsStart: t.string,
            StatementEnd: t.string,
            Identifier: t.atom,
            BlockStart: t.string,
            BlockEnd: t.string,
            BlockComment: t.className,
            "( )": t.paren
        }),
        /*
        indentNodeProp.add(
            {
                Application: context => context.column(context.node.from) + context.unit
            }
        ),
        */
        foldNodeProp.add({
            //Application: foldInside,
            BlockComment: foldInside,
            Block: foldInside,
            Proof: foldInside
          })
        ]
    })
      
    console.log('parserMetadata')

    const exampleLanguage = LRLanguage.define({
        parser: parserWithMetadata,
        languageData: {
          commentTokens: {line: ";"}
        }
    })

    console.log('exampleLanguage')

    const exampleCompletion = exampleLanguage.data.of({
        autocomplete: completeFromList([
          {label: "defun", type: "keyword"},
          {label: "defvar", type: "keyword"},
          {label: "let", type: "keyword"},
          {label: "cons", type: "function"},
          {label: "car", type: "function"},
          {label: "cdr", type: "function"}
        ])
    })
    
    console.log('exampleCompletion')

    let langSupport = new LanguageSupport(exampleLanguage, [exampleCompletion])
    console.info('langSupport', langSupport)
    return langSupport
}
  


