import {WidgetType} from "@codemirror/view"
import {EditorView, Decoration} from "@codemirror/view"
import {syntaxTree} from "@codemirror/language"
import { ViewPlugin,} from "@codemirror/view"
  
function _optionalChain(ops) { let lastAccessLHS = undefined; let value = ops[0]; let i = 1; while (i < ops.length) { const op = ops[i]; const fn = ops[i + 1]; i += 2; if ((op === 'optionalAccess' || op === 'optionalCall') && value == null) { return undefined; } if (op === 'access' || op === 'optionalAccess') { lastAccessLHS = value; value = fn(value); } else if (op === 'call' || op === 'optionalCall') { value = fn((...args) => value.call(lastAccessLHS, ...args)); lastAccessLHS = undefined; } } return value; }//!placeholderMatcher

class SymbolWidget extends WidgetType {
  constructor(name) {
    super();
    this.name = name;
    console.log('SymbolWidget.constructor:name=' + name)
  }
  eq(other) {
    return this.name == other.name
  }
  toDOM() {
    let elt = document.createElement("span");
    
    elt.style.cssText = `
    border: 1px solid blue;
    border-radius: 4px;
    padding: 0 3px;
    background: lightblue;`

    if (this.name == '|-' )
        elt.innerHTML = "&#8866;";
    else if (this.name == '->')
        elt.innerHTML = "&#8866;";
    else if (this.name == 'ph')
        elt.innerHTML = '&#x1D711;'
    else if (this.name == 'ch')
        elt.innerHTML = '&#x1D713;'
    else {
        elt.innerHTML = this.name
    }
    return elt
  }
  ignoreEvent() {
    return false
  }
}


function checkboxes(view) {
  let widgets = [];
  for (let {from, to} of view.visibleRanges) {
    syntaxTree(view.state).iterate({
      from, to,
      enter: (node) => {
        if (node.name == "Symbol") {
          const sym = view.state.doc.sliceString(node.from, node.to)
          let deco = Decoration.replace({
            widget: new SymbolWidget(sym)
          });
          widgets.push(deco.range(node.from,node.to));
        }
      }
    });
  }
  return Decoration.set(widgets)
}
  
export const replaceSymbolsPlugin = ViewPlugin.fromClass(
  class {
    constructor(view) {
      this.decorations = checkboxes(view);
    }

    update(update) {
      if (update.docChanged || update.viewportChanged)
        this.decorations = checkboxes(update.view);
    }
  }, {
    decorations: v => v.decorations,
    provide: plugin => EditorView.atomicRanges.of(view => {
      return _optionalChain([view, 'access', _ => _.plugin, 'call', _2 => _2(plugin), 'optionalAccess', _3 => _3.decorations]) || Decoration.none
    })
  }
);
  
  
  